// import OSS from 'ali-oss'

class UploadVideo {
  static partSize = 1024 * 1024; // 每个分片大小
  static maxSize = 1024 * 1024 * 200; // 最大200M
  static parallel = 3; // 同时上传的分片数
  static tryCount = 3; //每次上传尝试3次重试
  static checkpoints = {}
  static ossClient
  static fileName
  static region
  static bucket
  static api
  static token
  
  static decodeParam() {
    const obj = {}
    const url = decodeURI(window.location.href);
    const urlParams = url.split('?')[1];
    const params = urlParams.split('&');
  
    for (let i = 0; i < params.length; i++) {
      const kv = params[i].split('=');
      obj[kv[0]] = kv[1]
    }
    return obj;
  }
  /**
   * 获取参数
   */
  static getParams() {
    const reqParam = UploadVideo.decodeParam()
    UploadVideo.region= 'oss-cn-chengdu'
    UploadVideo.bucket = reqParam.bucket;
    UploadVideo.api = 'https://' + reqParam.url + '/api/v1/media';
    UploadVideo.token = reqParam.token
    
    // UploadVideo.region= 'oss-cn-chengdu'
    // UploadVideo.bucket= 'ukshare-s-t'
    // UploadVideo.api= 'https://www.ukshare.cn/api/v1/media'
    // UploadVideo.token= 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJybCI6InVyIiwiaWQiOjIsImVuIjoiZGV2IiwidGsiOiJhciIsImlhdCI6MTYwNzMyNDgwNiwiZXhwIjoxNjA3MzYwODA2fQ.vJcEvhhaiSrk69nF-X1mNREJ7fK1zDy4Qgr6Um3Z7Vg'
  }
  /**
   * 生成OSSClient
   */
  static async initOSSClient() {
    const credentials = await UploadVideo.getCredential()
    const { AccessKeyId, AccessKeySecret, SecurityToken } = credentials;
    UploadVideo.ossClient = new window.OSS({
      accessKeyId: AccessKeyId,
      accessKeySecret: AccessKeySecret,
      stsToken: SecurityToken,
      bucket: UploadVideo.bucket,
      region: UploadVideo.region,
    });
  }
  /**
   * 获取fileName
   * 获取sts
   */
  static async getCredential() {
    const { api, token } = UploadVideo
    let result = await window.OSS.urllib.request(api, {
      method: 'POST',
      data: JSON.stringify({ type: 'PV', counts: 1 }),
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
    });    
    const data = JSON.parse(result.data)
    UploadVideo.fileName = data.medias[0]
    return data.sts
  }
}

export default UploadVideo