import React, { useEffect, useState } from 'react';
import icon from './static/up.png'
import UploadVideo from './model';
import './App.css';

const stateType = {
  'before': 'before',
  'loading': 'loading',
  'err': 'err',
}

function App() {
  const [state, setState] = useState(stateType.before)
  const [percent, setPercent] = useState(0)

  useEffect(() => {
    UploadVideo.getParams()
  }, [])

  /**
   * 点击按钮: 选择视频
   */
  function chooseVideo() {
    document.getElementById('fileInput')?.click()
  }
  /**
    * 完成视频选取, 开始上传 
   */
  async function onChange(e) {
    if(!e.target.files) {
      return
    }
    const fileList = Array.from(e.target.files);
    const file = fileList[0]
    console.log(file)
    if (file.size > UploadVideo.maxSize) {
      alert('上传的视频大小不能超过200M，请压缩后重新上传');
      return;
    }
    setState('loading')
    multipartUpload(file)
  }
  /**
   * 分片上传
   */
  async function multipartUpload(file) {
    const { ossClient, initOSSClient } = UploadVideo
    if (!ossClient) {
      await initOSSClient()
    }
    UploadVideo.ossClient.multipartUpload(UploadVideo.fileName, file, {
      parallel: UploadVideo.parallel,
      partSize: UploadVideo.partSize,
      progress: onMultipartUploadProgress,
    })
    .then(result => {
      window.wx.miniProgram.navigateBack();
      window.wx.miniProgram.postMessage({
        data: {
          returnCode: 'SUCCESS',
          fileName: UploadVideo.fileName,
        },
      });
    })
    .catch(err => {
      setState(stateType.err)
    });
  }  
  // 断点续传
  async function resumeMultipartUpload() {
    const { checkpoints, ossClient } = UploadVideo
    setState(stateType.loading)
    Object.values(checkpoints).forEach(checkpoint => {
      const { uploadId, file } = checkpoint;
      ossClient.multipartUpload(uploadId, file, {
          parallel: UploadVideo.parallel,
          partSize: UploadVideo.partSize, // 每个part 1MB
          progress: onMultipartUploadProgress,
          checkpoint,
        })
        .then(result => {
          delete checkpoints[checkpoint.uploadId];
          window.wx.miniProgram.navigateBack();
          window.wx.miniProgram.postMessage({
            data: {
              returnCode: 'SUCCESS',
              fileName: UploadVideo.fileName,
            },
          });
        })
        .catch(err => {
          setState('err')
        });
    });
  }  

  /**
   * 分片上传进度改变回调
   */ 
  async function onMultipartUploadProgress(progress, checkpoint) {
    let num = progress * 100;
    num = Number(num.toFixed(1));
    setPercent(num)
    UploadVideo.checkpoints[checkpoint.uploadId] = checkpoint;
  }

  return (
    <div className="page">
      {
        state === 'before' &&
        <div className="btn" onClick={chooseVideo} >
          <img src={icon} className="icon" alt='icon' />
          点击上传视频
        </div>
      }{
        state === 'loading' &&
        <div className="loading" id="loading">
          <div className="progress">
            <div className="circle loop">
              <div className="dot" id="dotrunning"></div>
            </div>
            <div className="num" id="percent">{percent}%</div>
          </div>
          <div className="info01">视频上传中...</div>
          <div className="info02">为确保视频成功上传，请勿离开本页哦！</div>
        </div>
      }{
        state === 'err' &&
        <div className="btn" onClick={resumeMultipartUpload} >
          <img src={icon} className="icon" alt='icon' />
          网络不稳定，点击尝试续传
        </div>        
      }
      <input type="file" id="fileInput" accept="video/mp4,video/3gp,video/m3u8," onChange={onChange} />
    </div>
  );
}

export default App;
